import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const date = r => require.ensure([], () => r(require('../page/demo/date.vue')), 'chunkname1')
const vuxlogin = r => require.ensure([], () => r(require('../page/vux/login')), 'chunkname4')
//用ui组件
export default new Router({
  //:type   路由属于传递的参数
  routes: [
     /***测试首页 */
    {
     path: '/',
     component: date
     },
     {
     path: '/vuxlogin',
     component: vuxlogin
     }
    ]
  })



